import markings from "./markings";
import colors from "./colors";

let markNum;
const petData = {
    base: {},
    markings: [],
    markNum: null
}

// PICK THE MARKINGS AND COLOR OF MARKINGS
const getPetData = () => {
    console.log('getting pet data')
    findMarkings()
    colorMarkings();
}

// Decides the number of markings
const findMarkings = () => {
    markNum = Math.floor(Math.random() * 5);
    console.log(`number of Markings: ${markNum}`)
    let i;
    for (i = 0; i < markNum; i++ ) {
        let markTypeNum = Math.floor(Math.random() * markings.length)
        petData.markings.push({ 
            name: markings[markTypeNum].name,
            src: markings[markTypeNum].src
        })
        console.log(markings[markTypeNum].src)
        petData.markNum = markNum;
    }
}

const colorMarkings = () => {
    let f;
    let baseColor = colors[Math.floor(Math.random() * colors.length)];
    petData.base = {
        name: baseColor.name,
        id: baseColor.id
    }
    for (f = 0; f < petData.markings.length; f++) {
        let color = Math.floor(Math.random() * colors.length)
        petData.markings[f].color = colors[color].name
        petData.markings[f].colorId = colors[color].id
    }
    console.log(petData)
}

export { getPetData, petData }