import React, { useEffect, useRef, } from 'react';
import colors from './assets/colors'
import mapColors from './assets/mapColors';

const Canvas = (props) => {
    

    const petData = props.data;
    console.log(petData.markNum)
    // VARIABLES (DATA)
    // ====================
    const canvasRef = useRef(null);
    let img2, img3, img4, img5;
    let id;
    var img1 = new Image();
    const images = [img2, img3, img4, img5];
    let color = colors[petData.base.id]
    let color2;
    let markColor1, markColor2, markColor3, markColor4;
    const markColorVariables = [markColor1, markColor2, markColor3, markColor4]
    const markingColors = [];

    if (petData.markNum !== null) {
        for (let k = 0; k < petData.markNum; k++) {
            markingColors.push(colors[petData.markings[k].colorId])
        }
        console.log('colors ' + markingColors)

        for (let l = 0; l < markingColors.length; l++) {
            markColorVariables[l] = petData.markings[l].colorId;
        }
    }


    const beginImageAlter = async (canvas, ctx) => {
        await colorBase(canvas, ctx);
        await findMarkingSrc(canvas, ctx)
    }

    const colorBase = () => {
        let canvas = canvasRef.current;
        let ctx = canvas.getContext("2d")
        img1.crossOrigin = "Anonymous";
        img1.src = 'https://res.cloudinary.com/dw2l5yiim/image/upload/v1600027701/FURLET/furletMAP_bpck2t.png'
        img1.onload = function(){ alterImage(this, ctx, canvas); }
    }

    const alterImage = async (imageObj, ctx, canvas) => {     
        ctx.drawImage(imageObj, 0, 0);
        id = ctx.getImageData(0, 0, canvas.width, canvas.height);
        ctx.putImageData(id, 0, 0)
        colorImage(id, ctx); 
    }
    
    const findMarkingSrc = async (id, ctx, canvas) => {
        let j;
        for(j = 0; j < petData.markNum; j++) {
            let canvas = canvasRef.current;
            let ctx = canvas.getContext("2d")
            markColorVariables[j] = colors[petData.markings[j].colorId]
            images[j] = new Image();
            images[j].crossOrigin = "Anonymous";
            images[j].src = petData.markings[j].src[j]
            images[j].onload = function(){ alterImage(this, ctx, canvas); }
        }
    }

    const pixelColor = (i, selectedColor) => {
        id.data[i] = selectedColor[0];
        id.data[i + 1] = selectedColor[1];
        id.data[i + 2] = selectedColor[2];
    }

    // Uses the selected randomColor numbers to apply pallettes
    // ====================
    function colorImage(id, ctx) {
        let i;
        for (i = 0; i < id.data.length; i += 4) {
            // magenta - base
            if (id.data[i] === mapColors.base[0] && id.data[i + 1] === mapColors.base[1] &&  id.data[i + 2] === mapColors.base[2]) {
                pixelColor(i, color.base) 
            } else if (id.data[i] === mapColors.baseLine[0] && id.data[i + 1] === mapColors.baseLine[1] &&  id.data[i + 2] === mapColors.baseLine[2]) {
                pixelColor(i, color.line) 
            } else if (id.data[i] === mapColors.baseShade[0] && id.data[i + 1] === mapColors.baseShade[1] &&  id.data[i + 2] === mapColors.baseShade[2]) {
                pixelColor(i,  color.shade) 
            }
            if (petData.markNum !== null)
                {// yellow - marking 1
                if (id.data[i] === mapColors.marking1[0] && id.data[i + 1] === mapColors.marking1[1] &&  id.data[i + 2] === mapColors.marking1[2]) {
                    pixelColor(i, markColorVariables[0].base) 
                } else if (id.data[i] === mapColors.marking1Line[0] && id.data[i + 1] === mapColors.marking1Line[1] &&  id.data[i + 2] === mapColors.marking1Line[2]) {
                    pixelColor(i, markColorVariables[0].line) 
                } else if (id.data[i] === mapColors.marking1Shade[0] && id.data[i + 1] === mapColors.marking1Shade[1] &&  id.data[i + 2] === mapColors.marking1Shade[2]) {
                    pixelColor(i,  markColorVariables[0].shade) 
                } else if (id.data[i] === mapColors.marking2[0] && id.data[i + 1] === mapColors.marking2[1] &&  id.data[i + 2] === mapColors.marking2[2]) {
                    pixelColor(i, markColorVariables[1].base) 
                } else if (id.data[i] === mapColors.marking2Line[0] && id.data[i + 1] === mapColors.marking2Line[1] &&  id.data[i + 2] === mapColors.marking2Line[2]) {
                    pixelColor(i, markColorVariables[1].line) 
                } else if (id.data[i] === mapColors.marking2Shade[0] && id.data[i + 1] === mapColors.marking2Shade[1] &&  id.data[i + 2] === mapColors.marking2Shade[2]) {
                    pixelColor(i,  markColorVariables[1].shade) 
                } else if (id.data[i] === mapColors.marking3[0] && id.data[i + 1] === mapColors.marking3[1] &&  id.data[i + 2] === mapColors.marking3[2]) {
                    pixelColor(i, markColorVariables[2].base) 
                } else if (id.data[i] === mapColors.marking3Line[0] && id.data[i + 1] === mapColors.marking3Line[1] &&  id.data[i + 2] === mapColors.marking3Line[2]) {
                    pixelColor(i, markColorVariables[2].line) 
                } else if (id.data[i] === mapColors.marking3Shade[0] && id.data[i + 1] === mapColors.marking3Shade[1] &&  id.data[i + 2] === mapColors.marking3Shade[2]) {
                    pixelColor(i,  markColorVariables[2].shade) 
                } else if (id.data[i] === mapColors.marking4[0] && id.data[i + 1] === mapColors.marking4[1] &&  id.data[i + 2] === mapColors.marking4[2]) {
                    pixelColor(i, markColorVariables[3].base) 
                } else if (id.data[i] === mapColors.marking4Line[0] && id.data[i + 1] === mapColors.marking4Line[1] &&  id.data[i + 2] === mapColors.marking4Line[2]) {
                    pixelColor(i, markColorVariables[3].line) 
                } else if (id.data[i] === mapColors.marking4Shade[0] && id.data[i + 1] === mapColors.marking4Shade[1] &&  id.data[i + 2] === mapColors.marking4Shade[2]) {
                    pixelColor(i,  markColorVariables[3].shade) 
                }
                
            }
        }
        ctx.putImageData(id, 0, 0)
    }
    // ====================

    useEffect(() => {
        beginImageAlter();
        console.log(petData)
        // eslint-disable-next-line
    }, [])

    return (
        <div>
            <canvas 
                ref={canvasRef}
                width="250" 
                height="250"
            />
        </div>
            
    )
}

export default Canvas
