import React from 'react'

const PetInfo = (props) => {
    const petData = props.data
    return (
        <div className="container">
            <h4>Base color: {petData.base.name} </h4>
            <h5>Number of markings: {petData.markings.length}</h5>
            <br></br>
            <div>
                {petData.markings.map( marking => (
                    <p><strong>{marking.name}</strong> Â· {marking.color}</p>
                ))}
            </div>
        </div>
    )
}

export default PetInfo
